<template>
  <main class="app-main">
    <section class="top">
      <p class="title">
        <md-icon class="go-back" name="arrow-left" size="lg" @click="goBack"></md-icon>对接详情
      </p>
    </section>

    <div class="con-con">
      <md-cell-item title="使用人" :addon="userInfo.name" />
      <md-cell-item title="使用人类型" :addon=" this.$store.state.user.userInfo.roleId==1?'专家':'单位'" />
      <md-cell-item title="单位联系人" :addon="userInfo.unit" />
      <md-cell-item title="申请使用时间段" :addon="userInfo.dockingStartTime" />
      <md-cell-item title="联系电话" :addon="userInfo.phone" />
      <md-cell-item title="联系邮箱" :addon="userInfo.email" />
      <md-cell-item title="事情事项简述" :addon="userInfo.intro" />
    </div>
  </main>
</template>

<script>
import { Field, CellItem, Icon, InputItem, Button } from "mand-mobile";
import { see } from "@/apiUrl/personal/info";

export default {
  name: "see",
  filters: {
    formatDate(time) {
      time = time * 1000;
      let date = new Date(time);
      return formatDate(date, "yyyy-MM-dd");
    }
  },
  components: {
    [Field.name]: Field,
    [CellItem.name]: CellItem,
    [Icon.name]: Icon,
    [Button.name]: Button,
    [InputItem.name]: InputItem
  },
  data() {
    return {
      userInfo: {}
    };
  },
  mounted() {
    this.getInfo();
  },
  methods: {
    // 回退
    goBack() {
      this.$router.push("/personal");
    },
    getInfo() {
      var updata = {
        id: this.$route.query.id
      };
      see(updata).then(res => {
        this.userInfo = res.detail;
        console.log(this.userInfo);
      });
    }
  }
};
</script>

<style lang="stylus" scoped>
.con-con
  width: 100%
  margin: 0px auto
  margin-bottom: 60px
  padding-top: 88px
/deep/ .md-cell-item-right
  width: 350px
/deep/ .md-cell-item-content
  width: 200px
  padding-left: 30px
  margin-right: 20px
/deep/ .md-cell-item-title
  width: 200px
</style>
